.header {
  height: 70px;
  width: 100%;
  background: white;
  box-shadow: 0 3px 6px #00000029;

  &__container {
    height: 100%;
    margin: 0 auto;
    max-width: 1352px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: flex;
    text-decoration: none;

    p {
      color: #08A6E4;
      font-size: 32px;
      font-weight: 300;
    }
  }

  &__nav {
    &__list {
      display: flex;

      li {
        padding: 28px 18px 22px;
        border-bottom: 4px solid transparent;
        color: #A5A5A5;
        font-size: 11px;
        font-weight: bold;
        text-transform: uppercase;
        text-decoration: none;
        letter-spacing: 3px;
        margin-left: 30px;

        &:hover {
          color: black;
          border-color: #08A6E4;
          cursor: pointer;
        }
      }
    }
  }
}

