.benefits-section {
  width: 100%;
  padding: 70px 0 0;
  background-color: #f7f7f7;

  &__container {
    max-width: 1124px;
    margin: 0 auto;
  }

  &__box {

    &--first {
      background: white;
      margin: 37px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 370px;
      border: 1px solid #E8E9ED;
      border-radius: 5px;
      padding: 35px 35px 10px 35px;
      position: relative;
      z-index: 1;
    }

    &--second {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
      height: 370px;
      background-color: white;
      border: 1px solid #E8E9ED;
      border-radius: 5px;
      position: relative;
    }

    &--third {
      background-color: white;
      text-align: left;
      border: 1px solid #E8E9ED;
    }
  }

  &__content {
    max-width: 416px;
    margin: 90px 35px;
  }

  &__title {
    font-size: 36px;
    font-family: "Raleway", sans-serif;;
    color: black;
  }

  &__description {
    margin-top: 30px;
    line-height: 20px;
  }

  &__photo {
    &--first {
      margin-right: 117px;
      margin-bottom: 38px;

      img {
        width: 386px;
        height: 219px;
        margin: 75px 80px 25px 170px;
        position: relative;
        z-index: 1;
      }
    }

    &--second {
      img {
        width: 426px;
        height: auto;
        margin: 30px 192px 13px 70px;
        z-index: 1;
      }
    }

    &--third {
      background-color: #55dfb4;
      width: 440px;
      height: 370px;
      border: 1px solid #E8E9ED;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 60px;

      img {
        width: 295px;
        height: 208px;
        position: relative;
      }
    }
  }
}

.second-benefits-box .benefits-title {
  margin-bottom: 30px;
}

.third-box-position {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 38px;
}

.third-box-position .third-benefits-box {
  width: 623px;
  height: 370px;
}


.third-benefits-box h2 {
  text-align: left;
  font-size: 36px;
  font-family: sans-serif;
  margin-top: -50px;
  width: 350px;
}

.third-benefits-box p {
  text-align: left;
  font-family: sans-serif;
  padding-top: 30px;
}

.content_substance {
  height: 230px;
}

.content_substance::after {
  content: "";
  display: block;
  width: 328px;
  margin: 45px 0 20px 0;
  border-radius: 20px;
}

.line1::after {
  border: 2px solid #fb3b64;;
}

.line2::after {
  border: 2px solid #08a6e4;;
}

.line3::after {
  border: 2px solid #55dfb4;
}

.shape {
  position: relative;
}

.shape:nth-child(1)::before {
  content: "";
  width: 200px;
  height: 200px;
  background: #fb3b64;
  position: absolute;
  border-radius: 41px;
  right: 90px;
  top: 30px;
  z-index: 0;
  transform: rotate(45deg);
}