
.open-section {
  width: 100%;

  &__container {
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 130px;
  }

  &__title {
    font-family: "Raleway", sans-serif;;
    font-size: 49px;
    font-weight: 300;
    color: black;
    margin-bottom: 20px;
  }

  &__text {
    font-size: 16px;
    font-weight: normal;
    color: black;
    margin-bottom: 38px;
  }

  &__button {
    border-radius: 40px;
    cursor: pointer;
    display: inline-block;
    padding: 26px 160px;
    background: #08A6E4;
    box-shadow: 5px 10px 14px #07ACE629;
    text-decoration: none;
    color: white;
    font-weight: bold;
  }

  &__image {
    max-width: 1352px;
    width: 100%;
  }
}

//page-open-section -> .open-section
//page-open-container -> .open-section__container
//page-open-title -> .open-section__title
